.main-footer {
  background-color: $pale-gray;
}

.main-footer__wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;

  @include section-wrapper;

  @include tablet {
    padding-block-start: 48px;
    padding-block-end: 58px;
    flex-direction: row;
    gap: 30px;
  }

  @include desktop {
    padding-block-start: 50px;
  }
}

.main-footer__item {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  text-align: left;
  border-bottom: 2px solid $whity-gray;
  padding-block-start: 30px;
  padding-block-end: 30px;

  @include tablet {
    border: none;
    width: auto;
    padding: 0;
  }
}

.main-footer__social-list {
  @include tablet {
    margin: 0 auto;
  }
}

.main-footer__item:last-child {
  border: none;
}

.main-footer__item--logo {
  display: none;

  @include tablet {
    margin-block-start: 2px;
    display: initial;
    padding: 0;
    text-align: left;
  }
}

.main-footer__item--devs {
  padding-block-start: 22px;

  @include tablet {
    padding: 0;
  }
}

.main-footer__link {
  display: inline-block;
}

.main-footer__link--logo:hover {
  fill: #46c1ae;
}

.main-footer__link--logo:active {
  opacity: 0.3;
}


.main-footer__link--devs {
  text-decoration: none;
  color: $black;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  font-size: 19px;
  font-weight: bold;

  @include tablet {
    margin-inline-start: auto;
  }

  @include desktop {
    padding-block-end: 6px;
  }
}

.main-footer__link--devs:hover > .main-footer__devs-logo {
  fill: #46c1ae;
}

.main-footer__link--devs:active > .main-footer__devs-logo {
  fill: rgba(#46c1ae, 0.3);
}

.main-footer__devs-logo {
  fill: #63d1bb;
  margin-inline-start: 25px;

  @include tablet {
    margin-inline-start: 27px;
  }

  @include desktop {
    margin-inline-start: 30px;
  }
}
